@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 2.25em;
    padding: 0 .9375em;
    span{
        font-size: .875em;
    }
    &:focus {
        box-shadow: none;
    }
    img{
        width: 1.25em;
        display: block;
        flex-shrink: 0;
        &:last-child{
            margin-left: .3125em;
        }
    }
}

.m-btn-gray,
.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: $main1;
        color: #fff;
    }
}

.m-btn-gray{
    background-color: $gray;
    color: $lightBlack;
}

.m-btn-white-outline{
    border: 1px solid #FFFFFF;
    color: #fff;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
    }
}
