.page-top {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    min-height: 42.125em;
    display: flex;
    align-items: flex-end;
    padding-bottom: 8.75em;
  }
  &__content {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1em;
  }
  &__title {
    color: #ffffff;
  }
  &__text {
    p {
      font-size: 1em;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #FFFFFF;
      &:not(:last-child) {
        margin-bottom: 1em;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .page-top {
    &__inner {
      min-height: 25em;
      padding-bottom: 4.375em;
    }
    &__text {
      p {
        font-size: 0.875em;
      }
    }
  }
}