.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    padding: 0 1.67em;
    height: 3.17em;
    width: 100%;
    font-size: .75em;
    background-color: $gray;
    color: $black;
    border-radius: .17em;
    &::placeholder {
      color: $lightBlack;
    }
    &:focus {
      background-color: rgba($main1,.1);
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
        background-color: $primary;
      }
    }
    &:hover+label {
      color: $primary;
      &::before {
        background-color: $primary;
      }
    }
  }
  label {
    padding-left: 1.3125em;
    position: relative;
    cursor: pointer;
    min-height: 1.125em;
    color: $lightBlack;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: .125em;
      background-color: rgba(112, 112, 112, 0.2);
      width: .875em;
      height: .875em;
      left: 0;
      top: .25em;
      transition: all .100s;
      @include bgImgSize(100%);
    }
    span{
      font-size: .875em;
      line-height: 129%;
    }
  }
}

.daterangepicker {
  font-size: 1em;
  font-family: $font1;
  width: auto;
  right: 9.6875em !important;
  &:before,
  &:after{
    display: none;
  }
  .drp-calendar{
    max-width: unset;
    &.right{
      padding: 1.375em 1.625em;
    }
    &.left{
      padding: 1.375em 0 1.375em 1.625em;
    }
  }
  .calendar-table{
    table{
      display: block;
      width: auto;
    }
    thead{
      display: block;
      tr{
        &:first-child{
          align-items: center;
          grid-template-columns: 3em calc(100% - 6em) 3em;
          margin-bottom: 1.25em;
        }
        &:last-child{
          margin-bottom: 1em;
        }
      }
    }
    tbody{
      display: block;
      td{
        height: 2.8125em;
      }
    }
    th{
      display: block;
    }
    tr{
      display: grid;
      grid-template-columns: repeat(7,3em);
      align-items: center;
    }
    th,
    td{
      width: 100%;
      font-size: 1em;
      line-height: 140.02%;
      @include center;
    }
    th{
      font-weight: 400;
      &.month{
        font-size: 1.25em;
        line-height: 140.02%;
        font-weight: 400;
      }
      &.prev,
      &.next{
        width: 3em;
        height: 2.5625em;
        border: 1px solid #BBBBBB;
        border-radius: .125em;
        @include bgImgSize(1.25em);
        span{
          display: none;
        }
        &:hover{
          background-color: rgba(217, 217, 217, 0.15);
        }
      }
      &.prev{
        background-image: url(../img/prev.svg);
      }
      &.next{
        background-image: url(../img/next.svg);
      }
    }
    td{
      border: none;
      min-width: unset;
      &.in-range{
        background-color: transparent;
        position: relative;
        &:not(.active){
          &:before{
            content: '';
            position: absolute;
            width: 100%;
            height: 2.1875em;
            top: 50%;
            background-color: rgba($primary,.15);
            //z-index: -1;
            transform: translateY(-50%);
          }
        }
      }
      &.active{
        background-color: $primary;
        border-radius: .125em;
      }
      &.off{
        opacity: 0;
      }
    }
  }
  .drp-buttons{
    border: none;
    padding: 0 1.625em 1.375em;
    display: flex !important;
    justify-content: center;
    .applyBtn{
      margin: 0;
      height: 3.5em;
      width: 100%;
      font-size: .875em;
      @include center;
    }
  }
  .drp-selected,
  .cancelBtn{
    display: none;
  }

}


@media screen and (max-width: 992px){
  .daterangepicker{
    width: 23em;
    max-width: 100vw;
    font-size: .75em;
    right: 1.25em !important;
    .drp-calendar{
      padding: 1em !important;
    }
    .calendar-table{
      padding-right: 0 !important;
    }
  }
}


@media screen and (max-width: 576px){
  .daterangepicker{
    font-size: .625em;
    right: 1em !important;
  }
}
