.teacher {
  padding: 7.8125em 0 4.6875em;
  &__title {
    text-align: center;
  }
  &__top {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 1fr;
    margin-top: 3.75em;
    &-img {
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-desc {
      display: flex;
      justify-content: center;
      flex-direction: column;
      background: #F9F8F9;
      padding: 5em;
    }
    &-subtitle {
      font-size: 0.875em;
      line-height: 120%;
    }
    &-title {
      margin-top: 0.625em;
    }
    &-text {
      margin-top: 1.25em;
      p {
        font-size: 1em;
        line-height: 140%;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.teacher-card {
  padding: 1.875em;
  background: rgba(249, 248, 249, 0.25);
  @media screen and (min-width: 993px){
    &:nth-child(4n - 3),
    &:nth-child(4n){
      background: $gray;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__img {
    width: 4.5em;
    height: 4.5em;
    img {
      width: 100%;
      display: block;
    }
  }
  &__btn {
    font-size: 0.875em;
    line-height: 120%;
    opacity: 0.5;
    margin-left: 0.625em;
  }
  &__desc {
    max-width: 25.25em;
  }
  &__title {
    font-weight: 700;
    font-size: 1.5em;
    line-height: 120%;
    margin-top: 1.25em;
  }
  &__text {
    margin-top: 0.625em;
    p {
      font-size: 1em;
      line-height: 140%;
      opacity: 0.85;
    }
  }
}

.heads-laboratories {
  margin: 4.6875em 0;
  background: #0F1D40;
  &__inner {
    padding: 6.25em 0;
  }
  &__title {
    text-align: center;
    color: #ffffff;
  }
  &__items {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin-top: 4.375em;
  }
}

.heads-laboratories__card {
  width: calc(100% / 3);
  height: auto;
  min-height: 15.5em;
  background: rgba(249, 248, 249, 0.05);
  backdrop-filter: blur(0.125em);
  padding: 2.8125em;
  @media screen and (min-width: 993px){
    &:nth-child(5n - 4),
    &:nth-child(5n - 1){
      background: rgba(249, 248, 249, 0.1);
    }
  }
  &--big {
    width: calc(100% / 2);
    @media screen and (min-width: 993px){
      display: flex;
      align-items: center;
      flex-direction: row-reverse;
      justify-content: space-between;
    }
    .heads-laboratories__card-img {
      flex-shrink: 0;
      width: 6.8125em;
      height: 6.8125em;
      margin: 0 0 0 0.9375em;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  &-img {
    width: 3.8125em;
    height: 3.8125em;
    margin-bottom: 1.375em;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &-title {
    font-weight: 700;
    font-size: 1.25em;
    line-height: 120%;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  &-text {
    margin-top: 0.625em;
    p {
      font-size: 0.875em;
      line-height: 120%;
      color: #FFFFFF;
    }
  }
}


@media screen and (max-width: 992px) {
  .teacher {
    padding: 1em 0;
    &__top {
      margin-top: 1.25em;
      &-desc {
        padding: 1.875em;
      }
      &-text {
        p {
          font-size: 0.875em;
        }
      }
    }
  }

  .teacher-card {
    &__title {
      font-size: 1.375em;
    }
    &__text {
      p {
        font-size: 0.875em;
      }
    }
  }

  .heads-laboratories {
    margin: 1em 0;
    &__inner {
      padding: 2.5em 0;
    }
    &__items {
      margin-top: 2.5em;
    }
  }

  .heads-laboratories__card {
    width: calc(100% / 2);
    min-height: 12.5em;
    padding: 1.875em;
    .heads-laboratories__card-img {
      width: 2.5em;
      height: 2.5em;
      margin: 0 0 1.375em;
    }
    &-title {
      font-size: 1.125em;
    }
  }
}

@media screen and (max-width: 576px) {
  .teacher {
    &__top {
      grid-template-columns: 1fr;
      &-desc {
        padding: 0.9375em;
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
  }

  .teacher-card {
    padding: 0.9375em;
    &:nth-child(even) {
      background: $gray;
    }
  }

  .heads-laboratories__card {
    width: 100%;
    padding: 0.9375em;
    &:nth-child(even) {
      background: rgba(249, 248, 249, 0.1);
    }
  }
}
