.footer{
  color: #FFFFFF;
  background-color: $main1;
  &__logo{
    display: block;
    width: 14em;
    max-width: 100%;
    margin-top: 2.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    max-width: 18.75em;
    @include lastMb(2.125em);
    &-icon{
      height: 2.5em;
      width: 3.625em;
      flex-shrink: 0;
      img{
        height: 100%;
        width: auto;
        display: block;
      }
    }
  }
  &__title{
    font-weight: 700;
    font-size: 1.25em;
    line-height: 130%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__link{
    @include lastMb(1.125em);
    span{
      font-size: 1.125em;
      line-height: 127%;
    }
  }
  &__sites,
  &__left{
    display: flex;
    align-items: center;
  }
  &__sites{
    margin-left: 2.8125em;
  }
  &__site,
  &__c,
  &__link2{
    @include lastMr(1.5625em);
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
  &__c,
  &__link2 {
    color: $white50;
    &:hover{
      color: #fff;
    }
  }
  &__site{
    display: flex;
    align-items: center;
    img{
      flex-shrink: 0;
      width: .8125em;
      margin-right: .5em;
    }
  }
  &__by{
    display: block;
    width: 5.5em;
    flex-shrink: 0;
    margin-left: auto;
    &:hover{
      opacity: .7;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top{
  padding: 5.3125em 0 2.375em;
  display: flex;
  justify-content: space-between;
}

.footer-bottom{
  border-top: 1px solid rgba(255, 255, 255, 0.21);
  padding: 1.25em 0;
  display: flex;
  align-items: center;
}


@media screen and (max-width: 992px){
  .footer{
    &__sites,
    &__left{
      flex-direction: column;
      align-items: flex-start;
      margin: 0 0 1em;
    }
    &__site,
    &__c,
    &__link2{
      margin-right: 0 !important;
      @include lastMb(1em);
    }
    &__by{
      margin: 0;
    }
  }

  .footer-top{
    padding: 2em 0 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 4em 2em;
  }

  .footer-bottom{
    flex-direction: column;
    align-items: flex-start;

  }
}


@media screen and (max-width: 576px){
  .footer{
    &__item{
      @include lastMb(1.5em);
    }
    &__logo{
      margin: 0;
    }
    &__title{
      font-size: 1em;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      @include lastMb(.75em);
      span{
        font-size: .875em;
      }
    }
  }

  .footer-top{
    grid-template-columns: 100%;
    gap: 2em;
  }
}
