.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  position: relative;
  color: #FFFFFF;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    //background: rgba(15, 29, 64, 0.9);
    background-image: url(../img/main-bg3.svg);
    background-size: cover;
    background-position: center;
  }
  &__inner{
    min-height: $fullHeight;
    padding: 12.5em 0 19.375em;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: flex-end;
  }
  &__content{
    @include grid100gap(1.25em);
    max-width: 50.5em;
  }
  &__badges{
    display: flex;
    margin-bottom: .3125em;
  }
  &__badge{
    @include center;
    padding: 0 .625em;
    border: 1px solid $white50;
    height: 3.3125em;
    color: $white50;
    img{
      width: 1.5em;
      margin-right: .3125em;
      display: block;
      flex-shrink: 0;
    }
    span{
      opacity: .8;
    }
  }
  &__text{
    letter-spacing: 0.02em;
  }
}

.main-footer{
  z-index: 2;
  position: absolute;
  left: 0;
  bottom: 0;
  padding-left: 4.625em;
  width: 100%;
  display: grid;
  grid-template-columns: 36.237% 40.63% 23.133%;
  &__video{
    height: 100%;
    width: 100%;
    @include center;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    background-image: url(../img/line.webp);
    background-repeat: no-repeat;
    background-position: right 0 top .875em;
    background-size: auto 7.9375em;
    span{
      font-size: 1.375em;
      line-height: 137%;
    }
  }
}

.main-link{
  height: 11.875em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4.375em 0 1.75em;
  background: rgba($main1,.75);
  backdrop-filter: blur(2px);
  position: relative;
  &:hover{
    background: rgba($main1,.95);
    color: #fff;
  }
  &__arrow{
    position: absolute;
    right: 0;
    top: 0;
    width: 1.875em;
    height: 100%;
    @include center;
    background-color: $main1;
    img{
      width: 100%;
    }
  }
  &__info{
    @include grid100gap(.1875em);
    max-width: 16.25em;
    &:before{
      content: '';
      display: block;
      width: 1.4375em;
      height: .125em;
      background-color: #fff;
      margin-bottom: .75em;
    }
  }
  &__title{
    text-transform: uppercase;
  }
  &__nums{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $white50;
    .text28{
      color: #fff;
    }
  }
}

.edu{
  padding: 9.375em 0;
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.5625em);
    margin-top: 1em;
  }
}

.edu-card{
  min-height: 16.4375em;
  background-color: $gray;
  width: calc(50% - 1.5625em);
  margin: 1.5625em 1.5625em 0 0;
  display: flex;
  align-items: flex-end;
  position: relative;
  @media screen and (min-width: 993px){
    &:not(:hover){
      .edu-card__nav{
        margin: 0;
        height: 0;
        overflow: hidden;
      }
    }
    &._bachelor{
      width: calc(65.886% - 1.5625em);
      .edu-card__img{
        width: 24.125em;
      }
    }
    &._master{
      width: calc(100% - 65.886% - 1.5625em);
    }
    &._edu-center{
      .edu-card__nav{
        margin-top: .5em;
      }
    }
  }
  &._master{
    color: #fff;
    background-color: $red;
    .edu-card__img{
      right: 0;
      top: 0;
      position: absolute;
      height: 100%;
      width: unset;
      img{
        height: 100%;
        width: auto;
        display: block;
      }
    }
    .edu-card__faculty-title{
      &:before{
        background-color: #fff;
      }
    }
    .edu-card__btn{
      background-color: #fff;
      color: $red;
      &:hover{
        background-color: $main1;
        color: $red;
      }
    }
  }
  &__content{
    padding: 2.1875em 2.5em;
    position: relative;
    z-index: 2;
    max-width: 20em;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__info{
    @include grid100gap(.3125em);
  }
  &__faculty{
    margin-top: 1.25em;
    @include grid100gap(.5em);
    &-title{
      display: flex;
      align-items: center;
      &:before{
        content: '';
        display: block;
        flex-shrink: 0;
        width: 2.3125em;
        height: .125em;
        background-color: $black;
        margin-right: .5em;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
    transition: all .3s;
  }
  &__img{
    width: 16.3125em;
    margin-left: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__icon{
    height: 3.0625em;
    margin-bottom: auto;
    img{
      display: block;
      height: 100%;
    }
  }
}

.about{
  background-image: url(../img/about-bg.svg);
  background-size: cover;
  background-position: center;
  padding: 8.75em 0;
  color: #fff;
  &__title{
    text-align: center;
  }
  &__groups{
    margin-top: 9.25em;
    @include grid100gap(10em);
  }
  &__group{
    display: flex;
    justify-content: space-between;
    &:nth-child(even){
      flex-direction: row-reverse;
    }
  }
  &__content{
    width: 27.75em;
    align-self: center;
  }
  &__img{
    width: 28.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.8125em;
  }
  &__btn{
    margin-right: .9375em;
  }
}

.about-text{
  h3{
    font-weight: 700;
    font-size: 1.75em;
    line-height: 128.5%;
    @include lastMb(.71em);
  }
  h4{
    font-weight: 700;
    font-size: 1.25em;
    line-height: 160%;
    @include lastMb(1.25em);
    margin-top: 1.5em;
  }
  p{
    font-size: 1.125em;
    line-height: 178%;
    @include lastMb(.55em);
  }
  ul{
    @include grid100gap(1.5625em);
    li{
      font-size: 1.25em;
      line-height: 128%;
      padding-left: 1.44em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 1em;
        height: .125em;
        left: 0;
        top: .67em;
        background-color: #fff;
      }
    }
  }
}

.info{
  padding-bottom: 4.5em;
  display: grid;
  grid-template-columns: 33.375em calc(50% - 16.6875em) calc(50% - 16.6875em);
}

.info2{
  .info-card{
    overflow: hidden !important;
  }
}

.info-card{
  min-height: 21.875em;
  display: flex;
  align-items: flex-end;
  padding: 3.125em 2.5em;
  position: relative;
  background-color: $gray;
  overflow: hidden;
  @media screen and (min-width: 993px) {
    &._big {
      grid-column: 2/4;
      background-color: $primary;
      color: #FFFFFF;
      font-weight: 700;
      overflow: unset;
      .info-card {
        &__content {
          max-width: 25.5em;
        }

        &__num,
        &__title {
          font-size: 3em;
          line-height: 104%;
        }

        &__num {
          position: absolute;
          right: .625em;
          top: .625em;
        }

        &__img {
          width: 100%;
          height: auto;
          mix-blend-mode: luminosity;
        }
      }
    }
  }
  &._dark{
    background-color: $main1;
    color: #fff;
  }
  &__img{
    position: absolute;
    height: 100%;
    width: auto;
    bottom: 0;
    right: 0;
  }
  &__content{
    max-width: 18.375em;
    @include grid100gap(1.875em);
  }
  &__num,
  &__title{
    position: relative;
    z-index: 2;
    font-size: 2em;
    line-height: 119%;
  }
}

.info-conference{
  grid-row: 2/4;
  grid-column: 1/2;
  position: relative;
  display: flex;
  align-items: flex-end;
  padding: 3.875em 2.5em;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35));
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  &__content{
    max-width: 23.125em;
    color: #FFFFFF;
    position: relative;
    z-index: 3;
    @include grid100gap(.9375em);
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    height: 2.375em;
  }
}

.relematics{
  margin-top: 1.25em;
  padding: 4.5em 0;
  color: #fff;
  &__inner{
    min-height: 25em;
    position: relative;
    padding: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(56.2deg, #0F1D40 0%, rgba(15, 29, 64, 0.66) 113.83%);
      inset: 0;
      z-index: 2;
    }
    img{
      object-fit: cover;
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  &__title{
    text-align: center;
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: .625em;
  }
  &__item{
    margin: 0 .5em;
  }
  &__play{
    display: block;
    width: 4.1875em;
    margin: 2.1875em auto 0;
    img,
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    &:hover{
      circle{
        stroke: $primary;
      }
      path{
        fill: $primary;
      }
    }
  }
  .text{
    margin-top: 3.4375em;
  }
}

.news{
  padding: 4.5em 0;
  overflow: hidden;
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.5625em);
    margin-top: -1.5625em;
  }
}

.news-card{
  width: calc(100% / 3 - 1.5625em);
  margin: 1.5625em 1.5625em 0 0;
  position: relative;
  color: #fff;
  &._big{
    @media screen and (min-width: 993px){
      width: calc(50% - 1.5625em);
      .news-card{
        &__content{
          padding: 4em 2.8125em;
          max-width: 26em;
        }
        &__title{
          font-size: 2em;
        }
        &__desc{
          font-size: 1.125em;
        }
        &__btn{
          background-color: $primary;
          border-color: $primary;
          &:hover{
            background-color: $main1;
            border-color: $main1;
          }
        }
      }
    }
  }
  &__img{
    @include imgCover(98%);
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background: linear-gradient(47.99deg, rgba(0, 0, 0, 0.65) 0%, rgba(0, 0, 0, 0) 97.1%);
      z-index: 2;
    }
  }
  &__content{
    z-index: 3;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.875em 2.1875em;
    max-width: 20em;
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__title{
    font-size: 1em;
    line-height: 112%;
    font-weight: 700;
  }
  &__title,
  &__desc{
    @include truncate(2);
  }
  &__nav{
    display: flex;
    margin-top: 1.5625em;
  }
}

.contacts{
  padding: 4.5em 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
}

.contact-card{
  padding: 4.1875em .9375em 2.5em;
  background-color: $gray;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: #fff;
    .contact-card__title{
      color: #fff;
      &:after{
        background-color: #fff;
      }
    }
    a{
      &:hover{
        color: $white50;
      }
    }
  }
  &__title{
    color: $main1;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      display: block;
      flex-shrink: 0;
      width: 1.875em;
      height: .125em;
      background-color: $main1;
      margin-left: .3125em;
      margin-top: .2em;
      transition: inherit;
    }
  }
  &__info{
    margin-top: 1em;
    max-width: 11.25em;
    @include grid100gap(.5em);
  }
  &__item{
    font-size: .75em;
    line-height: 150%;
  }
  &__title,
  a{
    transition: unset;
  }
}

.map{
  height: 24.25em;
}



@media screen and (min-width: 993px) {
  @media screen and (max-height: 860px) {
    .main{
      &__inner{
        padding: 10em 0 15em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 2em 0;
      min-height: unset;
    }
    &__content{
      gap: 1em;
    }
  }

  .main-footer{
    padding: 0;
    position: relative;
    grid-template-columns: 100%;
    &__video{
      height: 3.5em;
      br{
        display: none;
      }
    }
  }

  .main-link{
    height: unset;
    padding: 1.25em 1.25em 3em;
    &__arrow{
      width: 100%;
      height: 1.875em;
      bottom: 0;
      top: unset;
      img{
        width: 1.875em;
        transform: rotate(90deg);
      }
    }
  }

  .edu{
    padding: 2em 0;
    .text28{
      font-size: 1.625em;
    }
    &__grid{
      width: 100%;
      margin: 0;
    }
  }

  .edu-card{
    width: 100%;
    margin-right: 0;
    min-height: unset;
    &__content{
      padding: 1.5em 1em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__icon{
      margin-bottom: 1.25em;
    }
  }

  .about{
    padding: 2em 0;
    &__groups{
      margin-top: 2em;
      gap: 2em;
    }
    &__group{
      flex-direction: column !important;
      .about__img{
        order: -1;
      }
    }
    &__img{
      width: 100%;
      max-width: 500px;
      margin: 0 auto 1.5em;
    }
    &__content{
      width: 100%;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .about-text{
    h3{
      font-size: 1.25em;
    }
    h4{
      font-size: 1.125em;
      margin-top: 1.25em;
      @include lastMb(.75em);
    }
    p{
      font-size: .875em;
    }
    ul{
      gap: .5em;
      li{
        font-size: .875em;
      }
    }
  }

  .info{
    grid-template-columns: 100%;
    overflow: hidden;
    padding-bottom: 1em;
  }

  .info-card{
    min-height: 11em;
    padding: 1.5em 1em;
    &:nth-child(6){
      background-color: transparent;
      color: $black;
    }
    &._big{
      background-color: $primary;
      color: #fff;
      .info-card__img{
        mix-blend-mode: luminosity;
        height: calc(100% + 1em);
        right: unset;
        left: 0;
      }
    }
    &__content{
      gap: 1.25em;
    }
    &__title,
    &__desc{
      font-size: 1.5em;
    }
  }

  .info-conference{
    min-height: 30em;
    grid-row: unset;
    padding: 4.5em 1em 1.5em;
    order: 10;
    &__nav{
      margin-top: 1em;
    }
  }

  .relematics{
    margin: 0;
    padding: 1em 0;
    &__inner{
      min-height: 18em;
      padding: 1.25em;
    }
    .text{
      margin-top: 2em;
    }
  }

  .news{
    padding: 1em 0;
  }

  .news-card{
    width: calc(50% - 1.5625em);
  }

  .contacts{
    padding: 1em 0 2em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .contact-card{
    padding: 1.875em 2.1875em;
  }
}


@media screen and (max-width: 576px){
  .main-footer{
    &__video{
      span{
        font-size: 1em;
      }
    }
  }

  .main-link{
    &__nums{
      display: none;
    }
  }

  .edu-card{
    &__img{
      display: none;
    }
  }

  .about{
    &__nav{
      @include grid100gap(.5em);
    }
    &__btn{
      margin: 0;
      width: 100%;
    }
  }

  .news{
    &__grid{
      width: 100%;
      margin-top: -1em;
    }
  }

  .news-card{
    width: 100%;
    margin: 1em 0 0;
    &__content{
      padding: 1em;
    }
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contact-card{
    padding: 2em 1em;
  }
}
