@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.block-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.5em;
  &__tabs{
    display: flex;
    align-items: center;
  }
  &__tab{
    height: 2.25em;
    padding: 0 .875em;
    display: flex;
    align-items: center;
    color: rgba($black,.5);
    border: 1px solid transparent;
    transition: all .3s;
    @include lastMr(.875em);
    span{
      font-size: .875em;
      line-height: 128%;
    }
    &._active,
    &:hover{
      border-color: $primary;
      color: $primary;
    }
    &._active{
      pointer-events: none;
    }
    &:hover{
      cursor: pointer;
    }
  }
}


@media screen and (max-width: 992px){
  .block-top{
    margin-bottom: 1.5em;
    display: block;
    &__tabs{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .block-top{
    &__tab{
      @include lastMr(.75em);
      padding: 0 .75em;
      span{
        font-size: .75em;
      }
    }
  }
}
