@import "../vars.scss";
.breadcrumbs {
    padding: 1.5625em 0 3.125em;
    &__item {
        font-size: 16px;
        line-height: 128%;
        position: relative;
        display: inline;
        color: rgba($black,.5);
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .072em;
                height: .36em;
                background-color: $black;
                top: .42em;
                right: 100%;
                margin-right: .5em;
            }
        }
    }
    @media screen and (min-width: 993px){
        &._type2{
            padding-bottom: 1.5625em;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0;
    }
}
