.text48{
  font-size: 3em;
  line-height: 115%;
}

.text40{
  font-size: 2.5em;
  line-height: 115%;
}

.text38{
  font-size: 2.375em;
  line-height: 129%;
}

.text28{
  font-size: 1.75em;
  line-height: 128%;
}

.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text18{
  font-size: 1.125em;
  line-height: 178%;
}

.text16{
  font-size: 1em;
  line-height: 131%;
}

.text14{
  font-size: .875em;
  line-height: 121%;
}

.text {
  leading-trim: both;
  text-edge: cap;
  letter-spacing: 0.02em;
  color: rgba($black,.9);
  h3{
    font-weight: 700;
    font-size: 1.5em;
    line-height: 133%;
    @include lastMb(1.25em);
  }
  h4{
    @include lastMb(1em);
  }
  h4,p{
    font-size: 1.25em;
    line-height: 140%;
  }
  p {
    letter-spacing: 0.02em;
    &:not(:last-child) {
      margin-bottom: 1.25em;
    }
  }
  hr{
    display: block;
    height: 2.25em;
    border: none;
  }
  a{
    color: $primary;
    text-decoration: underline;
  }
}

.bold-text{
  font-weight: 700;
}

.font2{
  font-family: $font2;
}

.font3{
  font-family: $font3;
}

.primary-text{
  color: $primary;
}

.light-black-text{
  color: rgba($black,.5);
}


@media screen and (max-width: 992px){
  .text48,
  .text38{
    font-size: 1.625em;
  }

  .text28{
    font-size: 1.25em;
  }

  .text18,
  .text16{
    font-size: .875em;
  }

  .text {
    h3{
      font-size: 1.25em;
      @include lastMb(1em);
    }
    h4{
      font-size: 1em;
    }
    p {
      font-size: 0.875em;
      &:not(:last-child) {
        margin-bottom: 0.9375em;
      }
    }
    hr{
      height: 1em;
    }
  }
}
