.news-page{
  padding-bottom: 4.5em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.125em / 4));
    gap: 1.375em;
  }
  &__bottom{
    margin-top: 4.375em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    background-color: $gray;
    color: $lightBlack;
  }
  &__showed{
    margin-top: 1.25em;
    text-align: center;
    color: $lightBlack;
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}

.filter{
  margin-top: 2.5em;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &__radios{
    display: flex;
    margin-right: auto;
  }
  &__radio{
    position: relative;
    @include lastMr(.875em);
    input{
      position: absolute;
      inset: 0;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
      &:hover,
      &:checked{
        &+label{
          border-color: $primary;
          color: $primary;
        }
      }
    }
    label{
      transition: all .3s;
      display: flex;
      align-items: center;
      height: 2.375em;
      padding: 0 .875em;
      border: 1px solid transparent;
      color: rgba($black,.5);
      span{
        font-size: .875em;
        line-height: 128%;
      }
    }
  }
  &__fields,
  &__checks{
    display: flex;
  }
  .m-check{
    @include lastMr(1.25em);
  }
  &__fields{
    margin-left: 1.875em;
  }
  &__field{
    width: 13.625em;
  }
  &__period{
    width: 2.375em;
    height: 2.375em;
    flex-shrink: 0;
    border-radius: .125em;
    @include center;
    position: relative;
    margin-left: .625em;
    background-color: $gray;
    &:hover{
      background-color: $primary;
      path{
        stroke: #fff;
      }
    }
    svg,
    img{
      width: 1.25em;
      height: auto;
    }
    input{
      position: absolute;
      inset: 0;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
    }
  }
}

.news-card2{
  display: block;
  &:hover{
    color: $black;
    .news-card2__img{
      img{
        transform: scale(1.1);
      }
    }
  }
  &__img{
    overflow: hidden;
    @include imgCover(82.1%);
    img{
      transition: all .3s;
    }
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
  &__title{
    font-weight: 700;
    font-size: .9375em;
    line-height: 133%;
    opacity: .8;
  }
  &__desc{
    font-size: .75em;
    line-height: 167%;
    opacity: .75;
  }
}

.news-item{
  padding-bottom: 4.5em;
  &__content{
    max-width: 56.25em;
    margin: 0 auto;
  }
  &__img{
    display: block;
    width: 100%;
  }
  &__props{
    margin: 2.75em 0 .375em;
    display: flex;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: 3.125em;
  }
  &__tag{
    @include center;
    padding: 0 .9375em;
    height: 3em;
    border-radius: 6.25em;
    margin: 1.25em 1.25em 0 0;
    background-color: $gray;
    leading-trim: both;
    text-edge: cap;
    color: $lightBlack;
    span{
      font-size: .875em;
    }
    &:hover{
      background-color: $primary;
      color: #fff;
    }
  }
  .text{
    margin-top: 2.5em;
  }
}

.news-prop{
  display: flex;
  align-items: center;
  leading-trim: both;
  text-edge: cap;
  color: $lightBlack;
  @include lastMr(1.75em);
  span{
    font-size: .875em;
    line-height: 130%;
  }
  img{
    width: .8125em;
    display: block;
    flex-shrink: 0;
    margin-right: .3125em;
  }
}

.share{
  display: flex;
  align-items: center;
  margin-top: 2.1875em;
  &__title{
    font-size: 1em;
    line-height: 150%;
    leading-trim: both;
    text-edge: cap;
    letter-spacing: 0.02em;
  }
  &__items{
    display: flex;
    align-items: center;
    margin-left: .625em;
  }
  &__item{
    width: 1.5em;
    margin-right: .9375em;
    img{
      width: 100%;
    }
  }
}

.news-collection{
  padding: 4.5em 0;
  overflow: hidden;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 4.125em / 4));
        gap: 1.375em;
      }
      &-slide{
        width: 100% !important;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .news-page{
    padding-bottom: 1em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__bottom{
      margin-top: 2em;
    }
  }

  .filter{
    display: block;
    margin-top: 1em;
    &__checks,
    &__fields{
      margin: 1em 0 0;
    }
    &__fields{
      display: grid;
      grid-template-columns: calc(100% - 3em) 2.375em;
      gap: .625em;
    }
    &__field{
      width: 100%;
    }
    &__period{
      margin: 0;
    }
  }

  .news-item{
    padding-bottom: 1em;
    &__props{
      margin-top: 1.5em;
    }
    .text{
      margin-top: 1.5em;
    }
    &__tags{
      margin-top: 1.5em;
    }
    &__tag{
      margin: .625em .625em 0 0;
      height: 2.5em;
    }
  }

  .news-prop{
    @include lastMr(1em);
  }

  .news-collection{
    padding: 1em 0;
    .swiper{
      &-slide{
        width: 18.75em;
        padding-right: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .filter{
    &__radios{
      flex-wrap: wrap;
      margin-top: -.75em;
    }
    &__radio{
      @include lastMr(.75em);
      margin-top: .75em;
      label{
        padding: 0 .75em;
        height: 2.25em;
        span{
          font-size: .75em;
        }
      }
    }
  }

  .news-collection{
    .swiper{
      &-slide{
        width: 17em;
        padding-right: 1em;
      }
    }
  }
}
