.developments {
  padding-bottom: 4.6875em;
  &__img {
    margin-bottom: 2.8125em;
    img {
      width: 100%;
      display: block;
    }
  }
  &__text {
    margin-top: 2.5em;
  }
  &__text-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2.8125em;
    p {
      font-size: 1.25em;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #000000;
      opacity: 0.9;
    }
    i {
      flex-shrink: 0;
      width: 28.625em;
      margin-left: 4.3125em;
      img {
        width: 100%;
        display: block;
      }
    }
  }
}

.graduates {
  padding: 4.6875em 0;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__btn {
    height: 2.25em;
    background: #F9F8F9;
    margin-left: 1.25em;
    padding: 0 0.9375em;
    span {
      font-size: 0.875em;
      line-height: 120%;
      opacity: 0.5;
    }
  }
  &__slider {
    position: relative;
    margin-top: 2.5em;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (1.375em * 3 / 4)));
      gap: 2.5em 1.375em;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
  }
}

.graduates-card {
  &__img {
    @include imgCover(82.1%);
  }
  &__desc {
    margin-top: 0.625em;
  }
  &__title {
    font-weight: 700;
    font-size: 0.9375em;
    line-height: 120%;
    opacity: 0.8;
  }
  &__text {
    margin-top: 0.625em;
    p {
      font-size: 0.75em;
      line-height: 160%;
      opacity: 0.75;
    }
  }
}


@media screen and (max-width: 992px) {
  .developments {
    padding-bottom: 1em;
    &__img {
      margin-bottom: 1.25em;
    }
    &__text {
      margin-top: 1.25em;
    }
    &__text-flex {
      margin-top: 1.25em;
      p {
        font-size: 0.875em;
      }
      i {
        width: 15.625em;
        margin-left: 1.25em;
      }
    }
  }

  .graduates{
    padding: 1em 0;
    overflow: hidden;
    &__slider {
      margin-top: 1.25em;
    }
    .swiper{
      overflow: unset;
      &-slide{
        width: 18em;
        &:not(:last-child) {
          padding-right: 1em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px) {
  .developments {
    &__text-flex {
      display: block;
      i {
        display: block;
        width: 100%;
        margin: 1.25em 0 0 0;
      }
    }
  }

  .graduates {
    &__top {
      display: block;
    }
    &__btn {
      margin: 0.75em 0 0 0;
    }
  }
}
